video {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
    pointer-events: none;
}

.hero-container {
    /* background: url('images/img-home.jpg') center center/cover no-repeat; */
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;
    
}   

.hero-container > h1 {
    color: #fff;
    font-size: 100px;
    margin-top: -100px;

}

/*small screen text*/
.hero-container > p {
    color: #fff;
    font-size: 32px;
    margin-top: 8px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

/*space between text and buttons*/
.hero-btns {
    margin-top: 32px;   
}

/*space between buttons*/
.hero-btns .btn{
    margin: 6px;
}

.fa-play-circle{
    margin-left: 4px;
}

@media screen and (max-width: 960px) {
    .hero-container > h1 {
        font-size: 70px;
        margin-top: -150px;
    }
}
    
@media screen and (max-width: 760px) {
    .hero-container > h1 {
        font-size: 50px;
        margin-top: -100px;
    } 

    .hero-container > p {
        font-size: 30px;
    }

    .btm-mobile{
        display: block;
        text-decoration: none;
    }

    .btn{
        width: 100%;
    }

}